import Vue from 'vue'
let vm=new Vue()

export const alertWarn=(msg)=>{
    vm.$message({
        type: 'warning',
        message: msg
      });
}

export const alertSuccess=(msg)=>{
    vm.$message({
        type: 'success',
        message: msg
      });
}


